$phone-width-small: 320px;
$phone-width-large: 425px;
$tablet-width: 768px;
$desktop-width: 1024px;

.header-component {
  .hidden {
    display: none;
  }

  .hamburger {
    outline: none;
    padding: 0 10px 0 0;
    .hamburger-box {
      height: 0;
      .hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
        width: 30px;
        transition-duration: 0.25s;
      }
    }
  }

  .items {
    display: flex;
    align-items: center;
    flex-direction: column;
    border-bottom: 1px solid black;
    padding-bottom: 20px;
    text-align: center;

    @include mobile() {
      border-bottom: none;
      flex-direction: row;
      justify-content: space-between;
    }
    @include mobile() {
      .spacer {
        width: 35px;
        margin-left: 0 20px;
      }
    }
    .menu-burger {
      width: 35px;
      margin-right: 20px;
    }

    .logo {
      max-width: 300px;
      padding: 20px 20px 0 20px;

      @include mobile() {
        max-width: 250px;
        justify-content: center;
      }
    }
    .subtitle {
      color: $color1;
      @include mobile() {
        display: none;
      }
    }
  }
}
